.content {
    grid-area: content;
    display: grid;
    grid-template-rows: 210px minmax(230px, auto);
    background-color: #ffffffff;
    box-shadow: 0px 0px 25px 0px rgba(45, 69, 95, 0.1);
    margin: 20px 120px 10px 100px;
    border-radius: 10px;

}

.banner {

    margin-bottom: 0;
    padding-bottom: 0;
}

.banner img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

.profileAndIcons {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}


.wrapperInput {
    height: 180px;
    width: 180px;
    position: relative;
    margin-left: 190px;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    overflow: hidden;
    border: 1px solid gray;

}


.photo {
    width: 100%;
    height: 100%;
}

.photoFile {
    width: 0.1px;
    height: 0.1px;
    overflow: hidden;

    opacity: 0;

}

.labelButton {
    height: 50px;
    position: absolute;
    width: 100%;
    text-align: center;
    transform: translate(-100%, 140%);
    background: rgba(0, 0, 0, .5);
    color: white;
    line-height: 30px;
    font-family: sans-serif;
    font-size: 15px;
    cursor: pointer;
    opacity: 0;
    bottom: 0;
    -webkit-transition: all .55s;
    -o-transition: all .55s;
    transition: all .55s;
}

.wrapperInput:hover .labelButton {
    opacity: 1;
    top: 60px

}


.statusProfile p {
    font-size: 23px;
    color: rgba(0, 0, 0, 0.74);
    font-weight: bold;
    font-family: "Century Gothic";


}

.statusProfile {
    margin-top: -70px;
    text-align: center;
    font-weight: 200;
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
}

.social_icons {
    align-self: center;
}

.social_icons span {
    padding: 10px;
    border-radius: 5px;
}

.editProfile {
    background-color: #50b5ff;
    color: white;
    border-radius: 15px;
    border: none;
    width: 240px;
    height: 30px;
    font-size: 14px;
    margin: 15px 70px 15px 70px;

}

.editMode {
    color: #656565;
    border-radius: 15px;
    border: 1px solid transparent;
    width: 80px;
    height: 25px;
    font-size: 14px;
    margin: 10px;
    padding: 2px;
}

.active:hover {
    background-color: #50b5ff;
    color: white;
    border-radius: 15px;
    border: 1px solid transparent;
    width: 80px;
    height: 25px;
    font-size: 14px;
    margin: 10px;
    padding: 2px;

}

.profileData {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 5px;
    padding: 5px;
    line-height: 1.8;
    font-size: 14px;
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;

}

.form_control {
    margin: 5px;
    padding: 5px;
    height: 45px;
    line-height: 45px;
    background: transparent;
    border: 1px solid #656565;
    font-size: 14px;
    color: #656565;
    border-radius: 10px;
    width: 95%;
}


.buttonEditMode {
    text-align: end;
    margin-right: 10px;

}

.lookingForAJob {
    margin: 5px;
    padding: 5px;
    height: 45px;
    line-height: 45px;
    font-size: 14px;
    color: #656565;
}

.Error {
    color: white;
    font-family: Helvetica, Arial, sans-serif;
    border: 1px rgba(212, 40, 40, 0.75) solid;
    background-color: rgba(212, 40, 40, 0.75);
    padding: 5px;
    border-radius: 5px;
    max-width: 160px;
    text-align: center;
    align-items: center;
    margin-left: 610px;
}


