.app-wrapper {
    display: grid;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 14px;
    line-height: 1.8;
    text-decoration: none;
    color: rgba(76, 79, 73, 0.88);
    grid-template-areas: "h h h" "n c online";
    grid-template-columns: 1fr 13fr 1fr;
    grid-template-rows: 75px auto;


}

.app-wrapper button {

    border-radius: 15px;
    border: none;
    outline: none;
}

.app-wrapper a {
    color: #656565;
}

.app-wrapper-content {
    grid-area: c;
    box-shadow: 0 0 25px 0 rgba(45, 69, 95, 0.1);
    align-items: start;
    background-color: white;

}


a {
    text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    margin: 0px;
    line-height: 1.5;
}


::-webkit-scrollbar-track {
    background-color: rgba(0, 194, 216, 0.09);
}

::-webkit-scrollbar-thumb {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    background-color: #50b5ff;
}

::-webkit-scrollbar-thumb:hover {
    background-color: rgba(0, 0, 0, 0.58);
}


::-webkit-scrollbar {
    width: 7px;
}













