.tabs {
    grid-area: t;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    box-shadow: 0px 0px 25px 0px rgba(45, 69, 95, 0.1);
    margin-left: 100px;
    margin-right: 120px;
    margin-bottom: 10px;
    border-radius: 15px;
    text-align: center;
    height: 75px;
}

.active:hover  {
   background-color: #50b5ff;
    color: white;
    font-size: 14px;
    border-radius: 15px;
    text-align: center;
    padding-top: 25px;
}

.tab {
    font-size: 14px;
    border-radius: 15px;
    text-align: center;
    padding-top: 25px;
}
