.event {
    grid-area: event;
    display: grid;

    background-color: white;
    box-shadow: 0px 0px 25px 0px rgba(45, 69, 95, 0.1);
    align-items: start;
    margin: 10px 10px 10px 100px;
    border-radius: 10px;
    padding: 10px;
}

.title {
    display: grid;
    grid-template-columns: 1fr 1fr;
    border-bottom: 1px solid;
    border-color: rgba(0, 0, 0, 0.13);
}

.titleName {
    padding-left: 10px;
}

.titleNav {
    font-size: 14px;
    justify-self: end;
    padding-right: 10px;
}

.titleNav a {
    color: #50b5ff;
}

.active a:hover {
    color: rgba(0, 125, 183, 0.99);
}

.lifeEvent img {
    max-width: 100%;
    height: auto;
}

.textTime {
    text-align: center;
}
