.online {
    grid-area: online;
    display: grid;
    background-color: #ffffffff;
    box-shadow: 0px 25px 25px 0px rgba(45, 69, 95, 0.1);
    height: 91vh;
    padding: 10px;
    overflow-y: scroll;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 15px;
    line-height: 1.8;
    position: fixed;
    top: 75px;
    right: 0;
}

.online h6 {
    font-family: 'Poppins', sans-serif;
    color: #656565;
    font-size: 14px;
    text-align: center;
}

.online img {
    border-radius: 50%;
    max-width: 65px;
}

.more h6 {
    color: #50b5ff;
}