.items {
    grid-area: i;
    display: grid;
    background-color:white;
    box-shadow: 0px 0px 25px 0px rgba(45, 69, 95, 0.1);
    text-align: center;
    margin: 10px 10px 10px 100px;
    border-radius: 10px;
}

.item{
    margin-top: 15px;
    color: #50b5ff;
}

.item a{
    color: #50b5ff;
}
.active a:hover{
color: rgba(0,125,183,0.99);
}