
.formControl.error input,
.formControl.error textarea {
    border: solid rgba(212, 40, 40, 0.75) 2px;
}

.formControl.error span {
    color: white;
    font-family: Helvetica, Arial, sans-serif;
    background-color: rgba(212, 40, 40, 0.75);
    padding: 5px;
    border-radius: 5px;
}

.formSummerError {
    color: white;
    font-family: Helvetica, Arial, sans-serif;
    border: 1px rgba(212, 40, 40, 0.75) solid;
    background-color: rgba(212, 40, 40, 0.75);
    padding: 5px;
    margin: 5px;
    border-radius: 5px;
    max-width: 160px;
}

.headerLogin {
    background-color: #50b5ff;
    color: white;
    border-radius: 2px;
    width: 370px;
    padding: 8px;
    font-size: 16px;
    margin: 10px;
}

.loginForm {
    display: grid;
    grid-template-columns: 6fr 4fr;
    background-color: #50b5ff;
    width: 1365px;
    height: 550px;
}

.loginBlock {
    background: white;
    width: 390px;
    height: 100%;
    text-align: center;
}

.loginBlock1 h2 {
    font-family: "Century Gothic";
    font-weight: 400;
    line-height: 1.5;
    font-size: 40px;
    margin-top: 50px;
    margin-bottom: -50px;
}


.Logotip {
    text-align: center;
    margin-top: 90px;
    color: white;
    font-size: 16px;
}

.LogotipName img {
    max-height: 50px;
    max-width: 100%;
}

.FriendsLogo img {
    height: 250px;
    width: 450px;
    border-radius: 10px;
}

.Logotip h1 {
    font-size: 28px;
}

.MainForm {
    margin-top: 80px;
}
.MainForm Input{
    width:250px;
    border-radius: 5px;
    height: 25px;
    margin: 5px;


}

.body {
    width: 360px;
    height: 30px;
    margin: 10px;
    border-radius: 5px;

}
